.list {
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    padding: 0;
    margin-top: -20px;
    margin-left: -20px;
  }
  
  .item {
    margin-top: 20px;
    margin-left: 20px;
    max-width: 250px;
    flex-basis: calc((100% - 100px) / 5);
    background-color: #b1b0b0b7;
    box-shadow: 10px 5px 30px rgb(0, 0, 0);
    border-radius: 15px;
  }
  
  .title {
    text-align: center;
  }
  
  .description {
    text-align: center;
  }
  
  .img {
    display: block;
    width: 100%;
    height: 320px;
  }